.popup {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 100;
  transition: opacity .2s ease-out 0s, z-index 0s linear 0s; }
  .popup--isClosed {
    z-index: -1;
    opacity: 0;
    transition: opacity .2s ease-out 0s, z-index 0s linear .2s; }
  .popup-overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.8);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center; }
  .popup-window {
