    border: none;
    width: 24px;
    height: 24px;
    display: inline-block;
    position: absolute;
    right: 24px;
    top: 24px; }
    .popup-closeBtn::before, .popup-closeBtn::after {
      content: "";
      height: 1px;
      width: 24px;
      background-color: #666;
      transform-origin: 50% 50%;
      position: absolute;
      top: 50%;
      left: 50%;
      margin-left: -12px; }
    .popup-closeBtn::before {
      transform: rotate(45deg); }
    .popup-closeBtn::after {
      transform: rotate(-45deg); }
    .popup-closeBtn:hover::before, .popup-closeBtn:hover::after {
      background-color: #333; }
  .popup-title {
